import { NextRequest, NextResponse } from 'next/server';
import { fetchPublishedIssues } from '@/lib/firestoreService';

export const dynamic = 'force-dynamic';

/**
 * GET /api/publications/list
 * Firestore ÏßÅÏ†ë Ï°∞Ìöå (Backend Proxy Ï†úÍ±∞Îê®)
 */
export async function GET(request: NextRequest) {
    try {
        const { searchParams } = new URL(request.url);
        // status ÌååÎùºÎØ∏ÌÑ∞Í∞Ä ÏûàÏßÄÎßå, ÌòÑÏû¨ fetchPublishedIssuesÎäî 'released'Îßå Í∞ÄÏ†∏Ïò¥
        // ÌïÑÏöîÌïòÎã§Î©¥ service ÌôïÏû•Ïù¥ ÌïÑÏöîÌïòÏßÄÎßå, ÏùºÎã® Í∏∞Ï°¥ Î°úÏßÅ(released only) Ï§ÄÏàò

        const { issues, latestUpdatedAt } = await fetchPublishedIssues();

        return NextResponse.json({
            success: true,
            issues,
            latest_updated_at: latestUpdatedAt
        });

    } catch (error) {
        console.error('Publications list error:', error);
        return NextResponse.json(
            { success: false, error: 'Failed to fetch publications' },
            { status: 500 }
        );
    }
}
